import React from 'react';
import { Redirect } from 'react-router';
import copy from 'copy-to-clipboard';
import './NewOperation.scss';
import ConfirmButton from './buttons/ConfirmButton';

const onCopy = (msg) => {
    copy(msg);
    alert('copied!');
}

class NewOperation extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isRedirect: false
        }
    }

    onClick() {
        this.setState({ isRedirect: true });
    }

    render() {
        return (
            <div className="json-container">
                <pre style={{
                    display: 'block',
                    padding: '10px 30px',
                    margin: '0',
                    overflow: 'visible',
                }}
                    onClick={() => onCopy(JSON.stringify(this.props.json, null, 4))}>
                    {JSON.stringify(this.props.json, null, 4)}
                </pre>
                <div className="json-confirm">
                    <ConfirmButton onClick={() => this.onClick()}>ACCEPT</ConfirmButton>
                </div>
                {this.state.isRedirect ?
                    <Redirect to={{
                        pathname: '/sign',
                        state: {
                            json: this.props.json
                        }
                    }} /> : false}
            </div>
        );
    }
}
export default NewOperation;